<template>
  <div>
    <PageTitle/>

    <RealtimeRiskInfo :all="realtimeRisk.all"/>

    <div class="page-box">
      <PanelTitle title="위험도 높은 생산공정"
                  desc="실시간 위험도 높은 생산공정"/>

      <table class="default-table">
        <thead>
        <tr>
          <th>생산공정</th>
          <th>위험단계</th>
          <th>위험률</th>
          <th class="w200"></th>
        </tr>
        </thead>
        <tbody>
        <tr v-for="item in realtimeRisk.procs"
            :key="item.procs_id">
          <td>{{ item.procs_nm }}</td>
          <td>
            <RiskStepLabel
                :name="item.risk_step_nm"
                :code="item.risk_step_cd"/>
          </td>
          <td>{{ item.risk_num + item.risk_unit }}</td>
          <td>
            <router-link
                :to="{ path:'/analysis/process/' + item.procs_id, query: {procs_nm: item.procs_nm}}"
                class="btn">
              상세보기
              <img src="~@/assets/img/caret-right.png" alt="상세보기">
            </router-link>
          </td>
        </tr>
        </tbody>
      </table>
    </div>

    <div class="page-box">
      <PanelTitle title="위험도 높은 설비"
                  desc="실시간 위험도 높은 설비"/>

      <table class="default-table double-row-header">
        <thead>
        <tr>
          <th rowspan="2">생산공정</th>
          <th rowspan="2">이상설비</th>
          <th rowspan="2">위험도</th>
          <th colspan="10">이상 원인</th>
        </tr>
        <tr>
          <th>온도</th>
          <th>압력</th>
          <th>유량</th>
          <th>누출농도</th>
          <th>밸브고장</th>
          <th>밸브고장2</th>
          <th>펌프고장</th>
          <th>이동</th>
          <th>경사도</th>
          <th>표면온도</th>
        </tr>
        </thead>
        <tbody>
        <tr v-for="item in realtimeRisk.eqmt"
            :key="item.eqmt_id">
          <td>{{ item.procs_nm }}</td>
          <td>{{ item.eqmt_nm }}</td>
          <td>
            <RiskLabel
                is-double-row
                :code="item.risk_step_cd"
                :name="item.risk_step_nm"
                :num="item.risk_num"
                :unit="item.risk_unit"/>
          </td>
          <td><RiskLabel :text-data="item.CRIG103" is-double-row /></td>
          <td><RiskLabel :text-data="item.CRIG104" is-double-row /></td>
          <td><RiskLabel :text-data="item.CRIG105" is-double-row /></td>
          <td><RiskLabel :text-data="item.CRIG106" is-double-row /></td>
          <td>{{ item.PART104 }}</td>
          <td>{{ item.PART105 }}</td>
          <td>{{ item.PART106 }}</td>
          <td>{{ item.KSEC105 }}</td>
          <td>{{ item.KSEC106 }}</td>
          <td><RiskLabel :text-data="item.KSEC107" is-double-row /></td>
        </tr>
        </tbody>
      </table>
    </div>

    <div class="page-box">
      <PanelTitle title="설비 분석 데이터"
                  desc="실시간 위험도 높은 설비의 24시간 분석 데이터"/>

      <PanelTab :items="realtimeRisk.eqmt"
                display-prop-name="eqmt_nm"
                :emit-prop-list="['eqmt_id','eqmt_nm']"
                @change="facilityTabChanged"/>

      <PanelSubTitle title="센서 데이터 분석 위험도"
                     :desc="selectedEquipmentName"/>

      <ChartBox :raw-data="realtimeEquipmentCrig.list" chart-type="MAIN"/>

      <table class="default-table double-row-header">
        <thead>
        <tr>
          <th colspan="2" rowspan="2" class="w150">위험도</th>
          <th v-for="item in crigYmdList"
              :key="item.ymd"
              :colspan="item.count">
            {{ formatter.date(item.ymd) }}
          </th>
        </tr>
        <tr>
          <th v-for="(item, idx) in realtimeEquipmentCrig.list"
              :key="idx">
            {{ item.qtime }}시
          </th>
        </tr>
        </thead>
        <tbody>
        <tr>
          <td colspan="2">화재폭발</td>
          <td v-for="(item, idx) in realtimeEquipmentCrig.list"
              :key="idx">
            <RiskLabel :text-data="item.CRIG101" :is-double-row="true"/>
          </td>
        </tr>
        <tr>
          <td colspan="2">질식/중독</td>
          <td v-for="(item, idx) in realtimeEquipmentCrig.list"
              :key="idx">
            <RiskLabel :text-data="item.CRIG102" :is-double-row="true"/>
          </td>
        </tr>
        <tr>
          <td rowspan="4">이상원인</td>
          <td>온도</td>
          <td v-for="(item, idx) in realtimeEquipmentCrig.list"
              :key="idx">
            <RiskLabel :text-data="item.CRIG103" :is-delete-step-suffix="true"/>
          </td>
        </tr>
        <tr>
          <td>압력</td>
          <td v-for="(item, idx) in realtimeEquipmentCrig.list"
              :key="idx">
            <RiskLabel :text-data="item.CRIG104" :is-delete-step-suffix="true"/>
          </td>
        </tr>
        <tr>
          <td>유량</td>
          <td v-for="(item, idx) in realtimeEquipmentCrig.list"
              :key="idx">
            <RiskLabel :text-data="item.CRIG105" :is-delete-step-suffix="true"/>
          </td>
        </tr>
        <tr>
          <td>누출농도</td>
          <td v-for="(item, idx) in realtimeEquipmentCrig.list"
              :key="idx">
            <RiskLabel :text-data="item.CRIG106" :is-delete-step-suffix="true"/>
          </td>
        </tr>
        </tbody>
      </table>

      <PanelSubTitle title="예지보전 위험도"
                     :desc="selectedEquipmentName"/>

      <table
          class="default-table double-row-header"
          v-if="realtimeEquipmentPartDb.list.length">
        <thead>
        <tr>
          <th rowspan="2">위험 단계</th>
          <th rowspan="2">고장확률</th>
          <th rowspan="2">고장유형</th>
          <th rowspan="2">잔여수명</th>
          <th :colspan="realtimeEquipmentPartDb.list.length - 3">이상원인</th>
        </tr>
        <tr>
          <th v-if="realtimeEquipmentPartDb.list[3]">{{ realtimeEquipmentPartDb.list[3].risk_type_nm }}</th>
          <th v-if="realtimeEquipmentPartDb.list[4]">{{ realtimeEquipmentPartDb.list[4].risk_type_nm }}</th>
          <th v-if="realtimeEquipmentPartDb.list[5]">{{ realtimeEquipmentPartDb.list[5].risk_type_nm }}</th>
        </tr>
        </thead>
        <tbody>
        <tr>
          <td>
            <RiskLabel
                v-if="realtimeEquipmentPartDb.list[0]"
                :text-data="realtimeEquipmentPartDb.list[0].risk_value"
                :show-risk-label="false"/>
          </td>
          <td>{{ realtimeEquipmentPartDb.list[0].risk_value.split(':')[0] + '%' }}</td>
          <td>{{ realtimeEquipmentPartDb.list[1].risk_value }}</td>
          <td>{{ formatter.date(realtimeEquipmentPartDb.list[2].risk_value) }}</td>
          <td>{{ realtimeEquipmentPartDb.list[3].risk_value }}</td>
          <td>{{ realtimeEquipmentPartDb.list[4].risk_value }}</td>
          <td v-if="realtimeEquipmentPartDb.list[5]">{{ realtimeEquipmentPartDb.list[5].risk_value }}</td>
        </tr>
        </tbody>
      </table>


      <PanelSubTitle title="영상 데이터 분석 위험도"
                     :desc="selectedEquipmentName"/>

      <table class="default-table double-row-header">
        <thead>
        <tr>
          <th rowspan="2" class="w300">이동 위험</th>
          <th rowspan="2">경사도</th>
          <th rowspan="2">표면온도</th>
          <th colspan="3">이상원인</th>
        </tr>
        <tr>
          <th>이동</th>
          <th>경사도</th>
          <th>표면온도</th>
        </tr>
        </thead>
        <tbody v-if="realtimeEquipmentKsec.list.length">
        <tr>
          <td class="flex-in-td">
            <RiskLabel :text-data="realtimeEquipmentKsec.list[0].risk_value"/>
            <a v-if="realtimeEquipmentKsec.list[3]"
               :href="realtimeEquipmentKsec.list[3].risk_value"
               target="_blank">
              <button class="default-button">항공사진 &gt;</button>
            </a>
          </td>
          <td>
            <RiskLabel :text-data="realtimeEquipmentKsec.list[1].risk_value"/>
          </td>
          <td>
            <RiskLabel :text-data="realtimeEquipmentKsec.list[2].risk_value"/>
          </td>
          <td>{{ realtimeEquipmentKsec.list[4].risk_value }}</td>
          <td>{{ realtimeEquipmentKsec.list[5].risk_value }}</td>
          <td>{{ realtimeEquipmentKsec.list[6].risk_value }}</td>
        </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
import RealtimeRiskInfo from "@/components/panels/RealtimeRiskInfo";
import PanelTitle from "@/components/ui/PanelTitle";
import RiskStepLabel from "@/components/ui/RiskStepLabel";
import RiskLabel from "@/components/ui/RiskLabel";
import PanelTab from "@/components/ui/PanelTab";
import { mapState } from 'vuex';
import PanelSubTitle from "@/components/ui/PanelSubTitle";
import ChartBox from "@/components/chart/ChartBox";
import formatter from "@/modules/formatter";
import PageTitle from "@/components/panels/PageTitle";

// import PageTitle from "@/components/panels/PageTitle";
export default {
  name: "RealTimeRisk",
  components: {PageTitle, ChartBox, PanelSubTitle, PanelTab, RiskLabel, RiskStepLabel, PanelTitle, RealtimeRiskInfo},

  data(){
    return{
      selectedEquipmentId: '',
      selectedEquipmentName: '',
      formatter
    }
  },

  computed: {
    ...mapState([
        'realtimeRisk',
        'realtimeEquipmentCrig',
        'realtimeEquipmentPartDb',
        'realtimeEquipmentKsec'
    ]),
    crigYmdList() {
      let list = [];
      this.realtimeEquipmentCrig.list.forEach(item => {
        let temp = list[list.length - 1];
        if (temp && temp.ymd === item.ymd){
          temp.count++;
        } else {
          list.push({
            ymd: item.ymd,
            count: 1
          });
        }
      })
      return list
    }
  },

  created() {
    this.queryData();
  },

  methods:{
    queryData(){
      this.$store.dispatch('realtimeRisk')
          .then(() => {
            let info = {
              eqmt_id: this.realtimeRisk.eqmt[0].eqmt_id,
              eqmt_nm: this.realtimeRisk.eqmt[0].eqmt_nm
            }
            this.facilityTabChanged(info);
          })
    },

    facilityTabChanged(info){
      console.log(info)
      this.selectedEquipmentId = info.eqmt_id;
      this.selectedEquipmentName = info.eqmt_nm;
      this.$store.dispatch('realtimeEquipmentCrig', {equipmentId: info.eqmt_id});
      this.$store.dispatch('realtimeEquipmentPartDb', {equipmentId: info.eqmt_id});
      this.$store.dispatch('realtimeEquipmentKsec', {equipmentId: info.eqmt_id});
    }
  }
};
</script>

<style scoped lang="scss">
@import "src/assets/scss/table";
</style>